.profile-slider {
    margin: auto;
    --slide-spacing: 1.5rem;
    --slide-size: 100%;
    max-width: 100%;
    position: relative;

    @include media('>=small') {
        --slide-size:50%;
    }
    @include media('>=tablet') {
        --slide-size: 33%;
    }
    @include media('>=medium') {
        --slide-size: 25%;
    }
    @include media('>=1080p') {
        --slide-size: 20%;
    }
    
}

  .profile-slider__viewport {
    width: 100%;
    @include media('>=small') {
         width: 90%;
    }
    @include media('>=tablet') {
    }
    @include media('>=medium') {
        width: 96%;
    }
    @include media('>=extralarge') {
        width: 100%;
    }

  }
  




.profile-slider__btns {
    justify-content: flex-end;
    margin-bottom: $measure-small;
    @include media('>=medium') {
        position: absolute;
        top: -75px;
        right:0;
        margin-bottom:0;
    }
    &.profile-slider__btns--disabled {
        display: none;
    }
}
.profile-slider__viewport {
    overflow:visible
}
.profile-slider__inner-container {
    position: relative;
    overflow: hidden;
    @include media('>=extralarge') {
        overflow: visible;
    }
}
.profile-slider__slide {
    max-width: none !important;
}
.profile-slider__img {
    margin-bottom: $measure-small;
}
.profile-slider__details {
    margin-bottom: $measure-small;
}
.profile-slider__name {
    font-weight: $font-weight-bold;
}
.profile-slider__company {
    font-weight: $font-weight-bold;
}

.profile-slider__description {
    font-size: $font-size--smalltype;
    p {
        font-size: inherit;
    }
}

@each $level, $color in $courses-v23-palette {
    $text-color: nth($color, 3);
    $btn-color: nth($color, 3);
    .#{$level}-courses-v23 {
        .profile-slider__company {
            color:$text-color;
        }
        .profile-slider__btn {
            background: $btn-color;
        }
    }
}