.courses-v23__rankings {
    --icon-size: 4rem;
    background-color: $color-athens-grey;
    padding-bottom: $measure--medium3;
    .courses-v23__rankings__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }   
    .courses-v23__rankings__text {
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
         p {
            @include h3style();
         }
    }
        .courses-v23__rankings__item {
            display: grid;
            gap: $measure-smallish;
            grid-template-columns: var(--icon-size) auto;
            @include media('>=medium') {
            grid-template-columns: fit-content(var(--icon-size)) auto; //Allow the icon column to shrink to fit its content
               @include double-dotted-line(); 
                &:first-child {
                    padding-left: 0;
                    &::before{
                        display: none;
                    }
                }
            }
        }
    .courses-v23__rankings__items {
        display: grid;
        grid-template-columns: 1fr;
        column-gap: $measure-medium;
        row-gap: $measure-large;
        width: 100%;
         @include media('>=medium') {
            width: auto;
             grid-template-columns: repeat(3, auto);
         }
    }
    
    .courses-v23__rankings__item-text{
        display: flex;
        flex-direction: column;
        font-size: $font-size--mini;
        font-weight: $font-weight-bold;
    }

    .courses-v23__rankings__item-icon{
        display: flex;
        justify-self: center;
        img {
            height: var(--icon-size);
            max-width: var(--icon-size);
        }
    }
    .courses-v23__rankings__item-rank {
        @include font-family(meta);
        font-size: $font-size--eta;
        font-weight: $font-weight-bold;
        line-height: 1;
        
    }
}

@each $level, $color in $courses-v23-palette {
    $heading: nth($color, 25);
    $background: nth($color, 26);

    .#{$level}-courses-v23 {
        .courses-v23__rankings__item-rank {
            color: $heading;
        }
        .courses-v23__rankings {
            background-color: $background;
        }
    }
}
