////
/// Embla Slider Default Styles.
/// This needs to be imported highter up the chain than any pattern that uses it, so that the pattern can override these styles if needed.
/// https://www.embla-carousel.com/
/// Default basic styles for Embla Carousel will display a basic horizontal slider, single slide per view.
/// Customisation is should be done in a separate pattern file, using pattern related class names.  See profile-slider.scss for example.
/// To increase number of slides per view, set --slide-size CSS variable to a percentage value, e.g. 50% for 2 slides, 33.3333% for 3 slides etc.
////

.embla {
    margin: auto;
    --slide-spacing: 1.5rem;
    --slide-size: 100%;
    max-width: 100%;

  }

  .embla__viewport {
    overflow: hidden;
  }

.embla__container {
    display: flex;
    touch-action: pan-y pinch-zoom;
    margin-left: calc(var(--slide-spacing) * -1); 
}
.embla__slide {
    transform: translate3d(0, 0, 0);
    flex: 0 0 var(--slide-size);
    min-width: 0;
    padding-left: var(--slide-spacing);
    opacity: 0.5;
    transition: opacity 600ms ease-out, transform 600ms ease-out;
}

.embla__slide--is-in-view, .embla__slide--is-snapped {
    opacity: 1;
    transform: none;
    transition: opacity 600ms ease-out, transform 600ms ease-out;
}

.embla__btns {
    display: flex;
}
.embla__btn {
    position: relative;
    background: $color-black;
    color:$color-white;
    width: $measure--medium2;
    height: $measure--medium2;
    display: flex;
    justify-content: center;
    align-items: center;
   &:active {
        outline: 2px dotted $color-black;
        outline-offset: 3px;
    }
    &:focus {
        z-index: 1;
        outline: 3px solid $color-black;
        outline-offset: 3px;
    }

    &[disabled] {
        opacity: 0.5;
    }
}